import React from 'react';
import '../css/routingView.css';
var JSZip = require("jszip");
var FileSaver = require('file-saver');  

const patternPixelDimension = {width: 1155.3, height:801.6} 

const gloveButtons =  {
                     1:{ startPoint: [920,540] },
                     2:{ startPoint: [245,530] },
                     3:{ startPoint: [271,81] },
                     4:{ startPoint: [271,144] },
                     5:{ startPoint: [271,206] },
                     6:{ startPoint: [198,48]  },
                     7:{ startPoint: [198,119] },
                     8:{ startPoint: [198,191] },
                     9:{ startPoint: [117,81] },
                    10:{ startPoint: [117,144] },
                    11:{ startPoint: [117,206] },
                    12:{ startPoint: [40,145]  },
                    13:{ startPoint: [40,201]  },
                    14:{ startPoint: [40,256]  },
                    15:{ startPoint: [345,55]  },
                    16:{ startPoint: [430,29]  },
                    17:{ startPoint: [515,55]  },
                    18:{ startPoint: [594,124] },
                }

const staticConnections = {
                    "Thumb":  {endPoint:[945,780] } //Absolute Position 
                  }

const controller = { pins:{
                            "RX":   {endPoint:[71.528, 10.05 ]},  // Relative position 
                            "TX":   {endPoint:[53.95,12.455]},
                            "2":    {endPoint:[38.1, 19.845]},
                            "3":    {endPoint:[24.65, 31.335 ]},
                            "4":    {endPoint:[15.145 ,45.957 ]},
                            "GND":  {endPoint:[10.249, 62.65 ]},
                            "VOL":  {endPoint:[10.113,80.163]},
                            "5":    {endPoint:[15.105 ,96.919 ]},
                            "6":    {endPoint:[24.67 ,111.575]},
                            "7":    {endPoint:[37.995,123.05]},
                            "8":    {endPoint:[53.55,131.028]},
                            "9":    {endPoint:[71.45 ,132.628]}, 
                            "10":   {endPoint:[88.837,130.25 ]},
                            "11":   {endPoint:[104.907 ,123.05]},
                            "12":   {endPoint:[118.122 ,111.55]},
                            "13":   {endPoint:[127.645 ,96.855]},
                            "A0":   {endPoint:[132.596,80.213]},
                            "A1":   {endPoint:[132.65 ,62.705]},
                            "A2":   {endPoint:[127.705,45.854]},
                            "A3":   {endPoint:[118.155,31.3]},
                            "A4":   {endPoint:[104.949,19.905]},
                            "A5":   {endPoint:[88.75,12.561]}, 
                      },
                      wire:{
                        "width": "1mm",
                        "spacing": "5mm"
                      },
                      position: [96,629], //pixel position[x,y] of coordinate 0,0 
                      angle: 90,
                      size: [142, 142]   //pixel size [width, height]
} 

export class RoutingView extends React.Component{

  constructor(props) {  
    super(props);  
    this.handleButtonClick = this.handleButtonClick.bind(this);
    this.handleButtonMapping = this.handleButtonMapping.bind(this);
    this.handlePinName = this.handlePinName.bind(this);
    this.handleExportData = this.handleExportData.bind(this);
    this.handleOpenExplorerOnEnter = this.handleOpenExplorerOnEnter.bind(this);
    this.handleOpenExplorerOnClick = this.handleOpenExplorerOnClick.bind(this);
    this.handleOnLoadFile = this.handleOnLoadFile.bind(this); 
    this.state = { 
      activeButton: null,
      activePin: null,
      pinName: "", 
      mappings: {},
      activePins:[],
      wires:[],
      modalOpen: false,
      isBRDLoaded: false,
    };     
  } 

  componentDidMount(){ 
      let buttons = [];
      for(let button in this.props.designer.state.activeSensors){
        buttons.push(gloveButtons[this.props.designer.state.activeSensors[button]])
      } 
      this.props.designer.setState({buttonsPosition:buttons}); 
  }

  //Rotatation performed by the svg tag, the rotation angle is defined in the constant controller 
  rotateController(){
    let cx =  controller.position[0] + (controller.size[0]/2);
    let cy =  controller.position[1] + (controller.size[1]/2); 
    return `rotate(${controller.angle} ${cx},${cy})`
  }

  rotateCoordinate() {
    let cx =  controller.position[0] + (controller.size[0]/2);
    let cy =  controller.position[1] + (controller.size[1]/2); 
    return `rotate(${controller.angle} ${cx},${cy})`
  }

  //rotation in real pixel values 
  rotatePoint(pos){
    let angle = controller.angle * Math.PI / 180.0;
    let originX = controller.position[0]+(controller.size[0]/2);
    let originY = controller.position[1]+(controller.size[1]/2);
    let x = pos[0] + controller.position[0];
    let y = pos[1] +controller.position[1]; 
    let cx = Math.cos(angle) * (x-originX) - Math.sin(angle) * (y-originY) + originX;
    let cy = Math.sin(angle) * (x-originX) + Math.cos(angle) * (y-originY) + originY;
    return {x: cx, y:cy}
  }

  handleButtonClick(button) {

    if(this.state.modalOpen=== true){
      this.setState({modalOpen: false})
    }

    if(this.state.activePin ===null){
      this.setState({activeButton: button});
    }else{ 
      let mappings = this.state.mappings;
      mappings[button] = this.state.activePin; 
      this.setState({mappings: mappings});
      this.setState({activePin: null});
      let pins = []; 
      for(let btn in mappings){ 
        pins.push(mappings[btn]);
      } 
      this.setState({activePins: pins});
    }
  }

  handleButtonMapping(pin) { 



    if(this.state.activeButton !== null ){ 
      let mappings = this.state.mappings;
      mappings[this.state.activeButton] = pin; 
      this.setState({mappings: mappings});
      this.setState({activeButton: null});
      let pins = []; 
      for(let btn in mappings){ 
        pins.push(mappings[btn]);
      } 
      this.setState({activePins: pins});
    }else{
      this.setState({activePin: pin});
    } 
  }

  handlePinName(pin) { 
    this.setState({pinName:pin})
  }   

  handleExportData(){

   if(Object.keys(this.state.mappings).length !== this.props.designer.state.activeSensors.length){
    this.setState({modalOpen: true});
    return;
   }


    let schematic = "GRID MM; \n SET NET_WIRE_WIDTH 0.1; \n"; 
    let y = 0;
    for (let map in this.props.designer.state.activeSensors ) {
      let button = this.props.designer.state.activeSensors[map];
      let pin = this.state.mappings[button];  
      let net = `NET (0 `+y+`) (20 `+y+`); \n`;
      let addHoleButton = `ADD 'SEW_HOLE' BUTTON`+button+` MR0 (0 `+y+`); \n`;
      let addHolePin = `ADD 'SEW_HOLE-SMALL' PIN`+pin+` R0 (20 `+(y)+`); \n`; 
      y+=15; 
      schematic += net + addHoleButton + addHolePin;
    } 

    let board = "GRID MM; \n";
    
    for (let map in this.props.designer.state.activeSensors ) {
      let button = this.props.designer.state.activeSensors[map];
      let pin = this.state.mappings[button];   
      
      let pinCx = controller.pins[pin] !== undefined ? this.rotatePoint(controller.pins[pin].endPoint).x: staticConnections[pin].endPoint[0];
      let pinCy = controller.pins[pin] !== undefined ? this.rotatePoint(controller.pins[pin].endPoint).y: staticConnections[pin].endPoint[1] ;

      let moveHoleButton = `MOVE BUTTON`+button+` (`+
                            (gloveButtons[button].startPoint[0])+` `+
                            (gloveButtons[button].startPoint[1])+`);\n`;
      let moveHolePin  = `MOVE PIN`+pin+` (`+ pinCx +` `+ pinCy +`);\n`;
      board += moveHoleButton + moveHolePin;
    }

    let wireSetting = 'CLASS 0 Default '+controller.wire.width+' '+controller.wire.spacing+' 1mm;\n';
    let fireAutoRouting = 'AUTO'+'\n';
    board += wireSetting+fireAutoRouting; 

    var zip = new JSZip();      
    zip.file("2. schematic.scr", schematic ); 
    zip.file("3. board.scr", board ); 
    zip.file("1. outline.scr", outline ); 
    zip.generateAsync({type:"blob"}).then(function (blob) {  
         FileSaver.saveAs(blob, "eagleSCR.zip");                          
    }, function (err) { 
    
    }); 
  }

    handleOnLoadFile(){    
      let component = this;
      let files = this.refs.eagleBRD.files;   
      
      let  reader= new FileReader();
      reader.onload = function(e) {
        let allText = e.target.result; 
        let XMLParser = require('react-xml-parser');
        let xml = new XMLParser().parseFromString(allText);    // Assume xmlText contains the example XML
        let signals = xml.getElementsByTagName('signal');
        let wires = []; 
        for(let signal in signals){  
          Object.keys(signals[signal].children).reduce(function(r, e) {
            if (signals[signal].children[e].name === "wire")  wires.push(signals[signal].children[e].attributes)
          }, {}) 
        }  
        component.props.designer.setState({wires:wires});
        component.setState({isBRDLoaded:true});
      };
      reader.readAsText(files[0]);
    }

    handleOpenExplorerOnEnter(event){
      if ( event.keyCode === 13 || event.keyCode === 32 ) {  
          this.refs.eagleBRD.focus();  
          this.refs.eagleBRD.click();
      } 
    }

    handleOpenExplorerOnClick(event){ 
      this.refs.eagleBRD.focus();  
      this.refs.eagleBRD.click(); 
    }
  
  render (){  
      let height = this.props.designer.state.height;
      let width = this.props.designer.state.width; 
      let wires = this.props.designer.state.wires; 
      return(
      <div className="col grid routingViewWrapper">
        <div className="btnWiring"  onClick={this.handleExportData}>
            <span ref="popOver" className={(this.state.modalOpen===false)? "popOver hidden": "popOver shake"} style={{ right: "calc(100% + 10px)", top: "-2px"}}>
              <p>Some buttons have not been connected to a pin</p>
              <span className="pointer"></span>
            </span>  
             Download <br/>Eagle Scripts
        </div>

        <form className="uploadBRD">
          <div className="input-file-container">  
            <input className="input-file" ref="eagleBRD" type="file" onChange={this.handleOnLoadFile}/>
            <label tabIndex="0" htmlFor="thefile" className="input-file-trigger" onClick={this.handleOpenExplorerOnClick} onKeyDown={this.handleOpenExplorerOnEnter}>
              Load Eagle Board
            </label>
          </div>
          <p className="file-return"></p> 
        </form>  

        <div className="pinName" style = {{opacity: (this.state.pinName === "" ? "0":"1")}}>
          PIN {this.state.pinName}
        </div>
        <div className="scroll"> 
          <div className="mainWrapper" style={{width:  "100%" , height:height}}> 
                <svg  width={width}
                      height={height}
                      style ={{ padding:"25px 0"}}
                      viewBox={`0 0 ${patternPixelDimension.width} ${patternPixelDimension.height}`}>  
                      <image  xlinkHref={require('../img/glovePatternStroke.svg')}  
                              width={patternPixelDimension.width}
                              height={patternPixelDimension.height} />

                      <image  xlinkHref={require('../img/lilypad.svg')} 
                              x={controller.position[0]} 
                              y={controller.position[1]}  
                              width={controller.size[0]}
                              height={controller.size[1]}
                              transform={this.rotateController()}
                              /> 
                       {Object.keys(controller.pins).map(pin => (
                        <React.Fragment key={`pin${pin}`}> 
                                <circle id="pin"  
                                        cx={controller.pins[pin].endPoint[0]+controller.position[0]} 
                                        cy={controller.pins[pin].endPoint[1]+controller.position[1]} 
                                        r="5"  
                                        stroke="black" 
                                        strokeWidth="0.5" 
                                        fill={( this.state.activePins.includes(pin) === true || 
                                                this.state.activePin === pin)? "#FF004E": "#17c0b7"} 
                                        className="pin"
                                        transform={this.rotateCoordinate()} 
                                        onClick={() => this.handleButtonMapping(pin)} 
                                        onMouseEnter={() => this.handlePinName(pin)} 
                                        onMouseLeave={() => this.handlePinName("")} 
                                />  
                        </React.Fragment>                          
                        ))}  

                       {Object.keys(staticConnections).map(pin => (
                        <React.Fragment key={`static${pin}`}> 
                                <circle   
                                        cx={staticConnections[pin].endPoint[0]} 
                                        cy={staticConnections[pin].endPoint[1]} 
                                        r="5"  
                                        stroke="black" 
                                        strokeWidth="0.5" 
                                        fill={( this.state.activePins.includes(pin) === true || 
                                                this.state.activePin === pin)? "#FF004E": "#17c0b7"} 
                                        className="pin"
                                        onClick={() => this.handleButtonMapping(pin)} 
                                        onMouseEnter={() => this.handlePinName(pin)} 
                                        onMouseLeave={() => this.handlePinName("")} 
                                />  
                        </React.Fragment>                          
                        ))}  

                      {Object.keys(this.state.mappings).map(mapping => ( 
                        <React.Fragment key={`line${mapping}`}>
                          <line 
                            x1={gloveButtons[mapping].startPoint[0] } 
                            y1={gloveButtons[mapping].startPoint[1]} 
                            x2={(controller.pins[this.state.mappings[mapping]] !== undefined) ? this.rotatePoint(controller.pins[this.state.mappings[mapping]].endPoint).x : staticConnections[this.state.mappings[mapping]].endPoint[0]}
                            y2={(controller.pins[this.state.mappings[mapping]] !== undefined) ? this.rotatePoint(controller.pins[this.state.mappings[mapping]].endPoint).y : staticConnections[this.state.mappings[mapping]].endPoint[1] }
                            stroke="black" 
                            strokeDasharray="4"
                            strokeWidth="1"
                            className="line" 
                            strokeLinecap="round"
                            hidden = {this.state.isBRDLoaded === true? true:false}
                            />
                        </React.Fragment>
                      ))} 

                      {this.props.designer.state.activeSensors.map(button => ( 
                        <React.Fragment key={`sensor${button}`}>
                          <circle id="button"  
                                  cx={gloveButtons[button].startPoint[0]} 
                                  cy={gloveButtons[button].startPoint[1]} 
                                  r={this.state.activeButton === button ? "15":"5"} 
                                  stroke="black" 
                                  strokeWidth="0.5" 
                                  fill={(Object.keys(this.state.mappings).includes(button.toString()) === true ||
                                                     this.state.activeButton === button) ? "#FF004E": "#17c0b7"}
                                  onClick={() => this.handleButtonClick(button)} 
                                  className="sensorBtn"
                          />
                        </React.Fragment>
                      ))} 

                      {wires.map(wire => ( 
                        <React.Fragment key={`wire${wire.x1}${wire.y1}${wire.x2}${wire.y2}`}>
                          <line   
                                  x1 = {wire.x1}
                                  y1 = {wire.y1}
                                  x2 = {wire.x2}
                                  y2 = {wire.y2}
                                  stroke={wire.layer === "1" ? "#ff004e" : "blue"} 
                                  strokeWidth="2"   
                          />
                        </React.Fragment>
                      ))}   
                </svg>   
             
          </div>
        </div> 
      </div>
      );
  }
}

